const GREEN = '#4caf50';
const RED = '#fa5448';

class View {
    constructor() {
        this.validation_state = {
            name: false,
            phone: false,
            healthy: false,
            travel: false,
            covid: true,
        };
    }

    get all_varified() {
        let all_varified = true;
        for (const id in this.validation_state) {
            const is_passed = this.validation_state[id];
            if (!is_passed) {
                all_varified = false;
            }
        }
        return all_varified;
    }

    change_subtitle(text) {
        const subtitle_element = document.getElementById('subtitle');
        subtitle_element.innerText = text;
    }

    change_validation_state(id, message, is_passed) {
        // Â∞ÜÈ™åËØÅ‰ø°ÊÅØÂÇ®Â≠òËµ∑Êù•
        this.validation_state[id] = is_passed;

        // ÊîπÂèòUI
        const input_element = document.getElementById(id);
        const label_element = document.getElementById(`${id}_label`);

        let color = is_passed ? GREEN : RED;

        label_element.innerText = message;
        input_element.style = `background-color: ${color}; color: white; border: 2px solid ${color}`;
        label_element.style = `color: ${color}; font-weight: bold`;

        // ÊØèÂΩìÊîπÂèòUIÁöÑÊó∂ÂÄôÔºå‰πüË¶ÅÈ°∫‰æøÊ£ÄÊü•ÊåâÈíÆÊòØÂê¶ÂèØ‰ª•ÁÇπÂáª
        this.refresh_button();
    }

    refresh_button() {
        const button_element = document.getElementById('submit_button');
        if (!this.all_varified) {
            button_element.disabled = true;
            button_element.innerText = 'Êúâ‰ø°ÊÅØÊú™È™åËØÅÈÄöËøá';
        } else {
            button_element.disabled = false;
            button_element.innerText = 'Êèê‰∫§Êï∞ÊçÆ';
        }
    }

    refresh_image(id, image_file) {
        const image_element = document.getElementById(`${id}_image`);

        if (image_file) {
            const reader = new FileReader();
            reader.readAsDataURL(image_file);

            reader.onload = (e) => {
                const image_data = reader.result;

                image_element.src = image_data;
            };
        } else {
            image_element.src = '';
        }
    }
}

// ‰ª•‰∏ä‰∏∫view.js

const is_chinese = (string) => {
    var chinese = /[^\u4e00-\u9fa5]/;
    return !chinese.test(string);
};

const is_number = (string) => {
    var reg = /^[0-9]*$/;
    return reg.test(string);
};

class Model {
    constructor() {
        this.name = '';
        this.phone = '';
        this.healthy = null;
        this.travel = null;
        this.covid = null;
    }

    get date() {
        const date = new Date();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        let string;
        if (month <= 9) {
            string = `0${month}Êúà${day}Êó•`;
        } else {
            string = `${month}Êúà${day}Êó•`;
        }
        return string;
    }

    validate(id, value) {
        const MAX_IMAGE_SIZE = 5 * 1000 * 1024;

        let is_passed = false;
        let msg = '';

        switch (id) {
            case 'name':
                if (value.length == 0) {
                    is_passed = false;
                    msg = 'ÂßìÂêç‰∏çËÉΩ‰∏∫Á©∫';
                } else if (!is_chinese(value)) {
                    is_passed = false;
                    msg = 'ÂßìÂêçÂè™ËÉΩ‰∏∫Ê±âÂ≠ó';
                } else if (value.length > 6) {
                    is_passed = false;
                    msg = 'ÂßìÂêç‰∏çËÉΩË∂ÖËøá6‰∏™Â≠ó';
                } else if (value.length <= 1) {
                    is_passed = false;
                    msg = 'ÂßìÂêçËá≥Â∞ë‰∏∫2‰∏™Â≠ó';
                } else {
                    this.name = value;
                    is_passed = true;
                    msg = 'ÂßìÂêçÔºàÈÄöËøáÈ™åËØÅÔºâ';
                }
                break;
            case 'phone':
                if (value.length == 0) {
                    is_passed = false;
                    msg = 'ÊâãÊú∫Âè∑‰∏çËÉΩ‰∏∫Á©∫';
                } else if (!is_number(value)) {
                    is_passed = false;
                    msg = 'ÊâãÊú∫Âè∑Âè™ËÉΩ‰∏∫Á∫ØÊï∞Â≠ó';
                } else if (value[0] != '1') {
                    is_passed = false;
                    msg = 'ÊâãÊú∫Âè∑ÂøÖÈ°ª‰ª•1ÂºÄÂ§¥';
                } else if (value.length < 11) {
                    is_passed = false;
                    msg = 'ÊâãÊú∫Âè∑ÈïøÂ∫¶Â∞ë‰∫é11‰Ωç';
                } else if (value.length > 11) {
                    is_passed = false;
                    msg = 'ÊâãÊú∫Âè∑ÈïøÂ∫¶Â§ö‰∫é11‰Ωç';
                } else {
                    this.phone = value;
                    is_passed = true;
                    msg = 'ÊâãÊú∫Âè∑ÔºàÈÄöËøáÈ™åËØÅÔºâ';
                }
                break;
            case 'healthy':
                if (!value) {
                    is_passed = false;
                    msg = 'Êú™‰∏ä‰º†ÂõæÁâá';
                } else if (value.size > MAX_IMAGE_SIZE) {
                    is_passed = false;
                    msg = 'ÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá5MB';
                } else {
                    this.healthy = value;
                    is_passed = true;
                    msg = 'ÂÅ•Â∫∑Á†ÅÔºàÂ∑≤‰∏ä‰º†Ôºâ';
                }
                break;
            case 'travel':
                if (!value) {
                    is_passed = false;
                    msg = 'Êú™‰∏ä‰º†ÂõæÁâá';
                } else if (value.size > MAX_IMAGE_SIZE) {
                    is_passed = false;
                    msg = 'ÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá5MB';
                } else {
                    this.travel = value;
                    is_passed = true;
                    msg = 'Ë°åÁ®ãÁ†ÅÔºàÂ∑≤‰∏ä‰º†Ôºâ';
                }
                break;
            case 'covid':
                if (!value) {
                    is_passed = true;
                    msg = 'Ê†∏ÈÖ∏Ê£ÄÊµãÔºàÊú™‰∏ä‰º†Ôºâ';
                } else if (value.size > MAX_IMAGE_SIZE) {
                    is_passed = false;
                    msg = 'ÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá5MB';
                } else {
                    this.covid = value;
                    is_passed = true;
                    msg = 'Ê†∏ÈÖ∏Ê£ÄÊµãÔºàÂ∑≤‰∏ä‰º†Ôºâ';
                }
                break;
        }

        return {
            is_passed,
            msg,
        };
    }

    submit_data() {
        console.log(this);

        const formData = new FormData();
        let url = '/submit';

        formData.append('batch', this.date);
        formData.append('username', this.name);
        formData.append('mobile', this.phone);
        formData.append('qr_code', this.healthy);
        formData.append('travel_code', this.travel);
        formData.append('check_up', this.covid);

        fetch(url, {
            method: 'POST',
            body: formData,
        })
            .then((response) => response.json())
            .then((response) => {
                if (response.status == 200) {
                    alert(`‰∏ä‰º†Áä∂ÊÄÅÔºö${response.message}ÔºåÂèØÂÖ≥Èó≠Ê≠§È°µÈù¢ÔºåËøõÂÖ•Â≠¶Ê†°`);
                } else {
                    alert(`‰∏ä‰º†Â§±Ë¥•ÔºåÈîôËØØÁ†Å‰∏∫${response.status}ÔºåÈîôËØØÂÜÖÂÆπÔºö${response.message}`);
                }
            })
            .catch((error) => {
                console.log(error);
                alert(error.message);
            });
    }
}

// ‰ª•‰∏ä‰∏∫model.js

const model = new Model();
const view = new View();

// Ëé∑Âèñ‰ªäÊó•Êó•ÊúüÔºåÂπ∂ÊòæÁ§∫
view.change_subtitle(`${model.date} - ÂÅ•Â∫∑Á†Å‰∏ä‰º†ÈÄöÈÅì`);

// ÂÆûÊó∂Ê£ÄÊµãËæìÂÖ•Ê°ÜÔºåÂπ∂Ê†πÊçÆÁªìÊûúÂÜ≥ÂÆöËÉΩÂê¶ÁÇπÂáªÊúÄÂêéÁöÑÊåâÈíÆ
const input_handler = (event) => {
    // 1„ÄÅËé∑ÂèñËæìÂÖ•‰ø°ÊÅØ
    const id = event.target.id;
    let value = null;

    try {
        // 2„ÄÅÂ∞ùËØïËØªÂèñÂõæÁâáÔºåÂ¶ÇÊûúËÉΩËØªÂèñÔºåÈÇ£‰πàÂä†ËΩΩÂõæÁâá
        value = event.target.files[0];
        view.refresh_image(id, value);
    } catch (error) {
        if (error instanceof TypeError) {
            // 3„ÄÅÂ¶ÇÊûúÂõæÁâáËØª‰∏çÂá∫Êù•ÔºåËØ¥ÊòéÊòØinputËæìÂÖ•Ê°ÜÔºåÈÇ£‰πàvalue‰∏∫string
            value = event.target.value;
        } else {
            throw error;
        }
    } finally {
        // 4„ÄÅÊ£ÄÈ™åÊï∞ÊçÆÊòØÂê¶ÈÄöËøá
        const result = model.validate(id, value);
        // 5„ÄÅÊ†πÊçÆÁªìÊûúÊîπÂèòUI
        view.change_validation_state(id, result.msg, result.is_passed);
    }
};

const submit_data = () => {
    const button_element = document.getElementById('submit_button');
    button_element.innerText = '‰∏ä‰º†‰∏≠‚Ä¶‚Ä¶';
    model.submit_data();
};

// ÁõëÂê¨Áî®Êà∑ËæìÂÖ•
const name_element = document.getElementById('name');
name_element.addEventListener('input', input_handler);

const phone_element = document.getElementById('phone');
phone_element.addEventListener('input', input_handler);

const healthy_element = document.getElementById('healthy');
healthy_element.addEventListener('change', input_handler);

const travel_element = document.getElementById('travel');
travel_element.addEventListener('change', input_handler);

const covid_element = document.getElementById('covid');
covid_element.addEventListener('change', input_handler);

const submit_button = document.getElementById('submit_button');
submit_button.addEventListener('click', submit_data);
